import React, {Component} from 'react';

class Order extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            location_lat : 0.0,
            location_lon : 0.0,
            //it appears as if the credit api only takes in a number as a string
            card_number : ''
        };
        this.handleInputChange = this.handleInputChange.bind(this);
    }
    handleInputChange(event){
        const name = event.target.name;
        const value = event.target.value;
        this.setState({
            [name] : value
        });
    }
    formSubmitHandler = () => {
        console.log(this.state);
        //Hardcoding the company id as 1 for MVP, could get it from our api
        //
        fetch('http://credit.17-356.isri.cmu.edu/api/transactions',{
            method : 'POST',
            body : {
                companyId : 1,
                ammount : 54.43
            }
        }).then((response) => response.json())
        .then((responseJson) => {
          console.log(responseJson.id);
        })
        .catch((error) => {
          console.error(error);
    });
    }
    render(){
        return (
        <form>
        <label>
          Delivery Latitude
          <input
            name="location_lat"
            type="number"
            value={this.state.location_lat}
            onChange={this.handleInputChange} />
        </label>
        <br />
        <label>
          Delivery Longitude:
          <input
            name="location_lon"
            type="number"
            value={this.state.location_lon}
            onChange={this.handleInputChange} />
        </label>
         <br />
        <label>
          Credit Card Number:
          <input
            name="card_number"
            type="number"
            value={this.state.card_number}
            onChange={this.handleInputChange} />
        </label>
        <a role="button" className="btn btn-primary" onClick={this.formSubmitHandler} href="/menu"> Submit </a>
      </form>
      );
    }
}

export default Order;
